import React, { useState } from 'react';
import { useLocation } from 'react-router-dom';
import { Button, Row, Divider } from 'antd';
import { Form, Input } from 'formik-antd';
import { Formik } from 'formik';
import * as Yup from 'yup';

import queries from '../serverQueries';

import FormItem from './formItems/FormItem';

const formLayoutSchema = {
  labelCol: {
    xs: { span: 24 },
    sm: { span: 6 },
  },
  wrapperCol: {
    xs: { span: 24 },
    sm: { span: 12 },
  },
};

const validationSchema = Yup.object({
  nickName: Yup.string().required('  '),
  firstName: Yup.string().required('  '),
  lastName: Yup.string().required('  '),
  password: Yup.string()
    .required('  ')
    .min(8, '   - 8 ')
    .max(40, '   - 40 ')
    .matches(/^(?=.*[a-z])(?=.*[A-Z])(?=.*\d)./, '    A-Z, a-z, 0-9'),
  repeatPassword: Yup.string()
    .required('  ')
    .oneOf([Yup.ref('password'), null], '  '),
  email: Yup.string()
    .email(' email ')
    .required('  '),
});

const Registration = () => {
  const [loading, changeLoadingState] = useState(false);
  const query = new URLSearchParams(useLocation().search);
  const key = query.get('key');

  const register = token => async (values, { setStatus }) => {
    changeLoadingState(true);
    setStatus('');
    const res = await queries.registrationUser({ key: token, ...values });
    if (res === 1) {
      setStatus('      email');
    } else {
      setStatus(' token ');
    }
    changeLoadingState(false);
  };
  return (
    <Formik
      initialValues={{
        nickName: '',
        firstName: '',
        lastName: '',
        password: '',
        repeatPassword: '',
        email: '',
      }}
      validationSchema={validationSchema}
      onSubmit={register(key)}
    >
      {({ status }) => (
        <>
          {status && (
            <Row type="flex" justify="center">
              <h3>{status}</h3>
            </Row>
          )}
          <Form {...formLayoutSchema}>
            <FormItem label="" name="nickName">
              <Input name="nickName" />
            </FormItem>
            <FormItem label="" name="firstName">
              <Input name="firstName" />
            </FormItem>
            <FormItem label="" name="lastName">
              <Input name="lastName" />
            </FormItem>
            <Divider />
            <FormItem label="" name="password">
              <Input.Password name="password" />
            </FormItem>
            <FormItem label=" " name="repeatPassword">
              <Input.Password name="repeatPassword" />
            </FormItem>
            <Divider />
            <FormItem label="" name="email">
              <Input name="email" />
            </FormItem>

            <Row type="flex" justify="center">
              <Button type="primary" htmlType="submit" loading={loading}>
                
              </Button>
            </Row>
          </Form>
        </>
      )}
    </Formik>
  );
};

export default Registration;
