import React, { useContext } from 'react';
import PropTypes from 'prop-types';
import { Menu, Dropdown, Icon, Modal, Button, message } from 'antd';
import context from '../Context';
import serverQueries from '../../serverQueries';

const AdminMenu = ({ user, updateUser }) => {
  const {
    isLogin,
    user: { role },
  } = useContext(context);

  if (!isLogin || role !== 'ROLE_ADMIN') {
    return null;
  }

  const handleBan = dt => () =>
    serverQueries
      .blackListRequest(user.id, Date.now() + dt)
      .then(({ dateUnblock }) => {
        const accountNonLocked = dateUnblock === null;
        updateUser({ ...user, accountNonLocked });
      })
      .catch(() => message.error(', -  .   .'));

  const handleUnban = () => {
    serverQueries
      .unblockUser(user.id)
      .then(({ dateUnblock }) => {
        const accountNonLocked = dateUnblock === null;
        updateUser({ ...user, accountNonLocked });
      })
      .catch(() => message.error(', -  .   .'));
  };

  const openConfirm = (action, content) => () => {
    Modal.confirm({
      title: 'Confirmation',
      content: (
        <div>
             : <b>{content}</b>  : <b>{user.nickName}</b>
        </div>
      ),
      onOk: action,
    });
  };

  const oneHour = 60 * 60 * 1000;

  const menu = (
    <Menu>
      <Menu.Item onClick={openConfirm(handleBan(24 * oneHour), '  24 ')}>
          24 
      </Menu.Item>
      <Menu.Item onClick={openConfirm(handleBan(7 * 24 * oneHour), '  7 ')}>
          7 
      </Menu.Item>
      <Menu.Item onClick={openConfirm(handleBan(9999 * 24 * oneHour), ' ')}>
         
      </Menu.Item>
    </Menu>
  );

  return user.accountNonLocked ? (
    <Dropdown overlay={menu} trigger={['click']}>
      <Button className="ant-dropdown-link">
        . 
        <Icon type="down" />
      </Button>
    </Dropdown>
  ) : (
    <Button onClick={openConfirm(handleUnban, 'unban')}></Button>
  );
};

AdminMenu.propTypes = {
  user: PropTypes.instanceOf(Object).isRequired,
  updateUser: PropTypes.func,
};

AdminMenu.defaultProps = {
  updateUser: () => {},
};

export default AdminMenu;
